import { Component, OnInit } from '@angular/core';
import { keyframes, animate, style, transition, state, trigger } from '@angular/animations';

@Component({
  selector: 'app-trainings',
  templateUrl: './trainings.component.html',
  styleUrls: ['./trainings.component.css'],
  animations: [
    trigger('openClose', [

      transition('void => *', [
        style({ opacity: -10}),
        animate('2s',)
      ]),
      transition('* => void', [
        //style({ backgroundColor: 'red', opacity: 1, transform: 'rotateX(180deg)' }),
        animate('1s ease-out', keyframes([
           style({transform:'translateX(-50%)'}),
           style({transform:'translateX(50%)'}),
                   ])) 
      ])
  ]),
    trigger('openClose1', [
    state('true', style({opacity: 1})),
    state('false', style({ opacity: 1})),
    transition('false <=> true',
      
      animate('5s ease-in',
        //style({ backgroundColor: 'green', opacity: 1, transform: 'rotateY(180deg)' })
        keyframes([
          style({transform:'translateY(-300px)'}),
          style({transform:'translateY(200px)'}),
          style({transform:'rotateY(180deg)'}),
          style({transform:'rotateY(180deg)'})
       ])) 
      
    )]),
      ]  

})
export class TrainingsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

}
