import { Component, OnInit } from '@angular/core';
import { trigger, state, style, transition, animate, keyframes } from '@angular/animations';

@Component({
  selector: 'app-meetspeaker',
  templateUrl: './meetspeaker.component.html',
  styleUrls: ['./meetspeaker.component.css'], animations: [
    trigger('openClose', [

      transition('void => *', [
        style({ opacity: -10}),
        animate('2s',)
      ]),
      transition('* => void', [
        //style({ backgroundColor: 'red', opacity: 1, transform: 'rotateX(180deg)' }),
        animate('1s ease-out', keyframes([
           style({transform:'translateX(-50%)'}),
           style({transform:'translateX(50%)'}),
                   ])) 
      ])
  ]),
    trigger('openClose1', [
    state('true', style({opacity: 1,transform:'rotateY(180deg)'})),
    state('false', style({ opacity: 1,transform:'rotateY(180deg)'})),
    transition('false <=> true',
      
      animate('1s ease-in',
        //style({ backgroundColor: 'green', opacity: 1, transform: 'rotateY(180deg)' })
        keyframes([
          style({transform:'rotateY(-90deg)'}),
          style({transform:'rotateY(180deg)'}),
          
       ])) 
      
    )]),
      ]

})
export class MeetspeakerComponent implements OnInit {

  constructor() { }
  name = 'AIM (Ambition in Mind)';
  regflag = true;
  isActive_sourabh="";
  isActive_lokesh="";
  isActive_krishan="";
  isActive_abhina="";
   
  ngOnInit(){
    // setInterval(()=>{
    //   //this.regflag=!this.regflag;
    //   },1000)
  }
   setActive(target){
     
  this.isActive_sourabh="";
  this.isActive_lokesh="";
  this.isActive_krishan="";
  this.isActive_abhina="";
  this[target]="active"

}
}