import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { AppComponent } from './app.component';
import { HelloComponent } from './hello.component';
import { TrainingsComponent } from './trainings/trainings.component';
import { MeetspeakerComponent } from './meetspeaker/meetspeaker.component';
import { RegistrationComponent } from './registration/registration.component';
import {RouterModule, Routes} from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutusComponent } from './aboutus/aboutus.component';


const routePath:Routes =[
{path:'trainings',component:TrainingsComponent},
{path:"meetspeaker",component:MeetspeakerComponent},
{path:"enroll",component:RegistrationComponent},
{path:'home',component:HomeComponent},
{path:'about',component:AboutusComponent},
{ path: '',
redirectTo: 'home',
pathMatch: 'full'
},
{ path: '**', component: AppComponent}
]
@NgModule({
  imports:      [ BrowserModule, FormsModule,BrowserAnimationsModule, RouterModule.forRoot(routePath)],
  declarations: [ AppComponent, HelloComponent, TrainingsComponent, MeetspeakerComponent, RegistrationComponent, HomeComponent, AboutusComponent ],
  bootstrap:    [ AppComponent ],
  providers:[]
})
export class AppModule { }
